<template>
  <div class="pairingListBox">
    <div class="pairingListBoxMain">
      <ul>
        <li v-for="pairing in showPairingList" :style="{backgroundColor: pairing.bgc}">
          <div class="username">
            <div class="usernameMain">{{pairing.username}}</div>
          </div>
          <div class="content" v-html="pairing.content"></div>
          <div class="time">日期：{{pairing.time}}</div>
          <div v-if="type == 'default'" class="accept" @click="accept(pairing)">我可以</div>
          <div v-else-if="type == 'delete'" class="delete" @click="remove(pairing)">删除</div>
        </li>
      </ul>
    </div>
    <div class="addPairingRequest" @click="addStatus = true"></div>
    <div class="deletePairingRequest" @click="deletePairing"></div>
    <div class="baffles" v-if="addStatus"></div>
    <div class="addPairingRequestBox" v-if="addStatus">
      <div class="title">发起配对</div>
      <textarea v-model="addContent"></textarea>
      <button class="submit" @click="submit">提交</button>
      <button class="close" @click="addStatus = false">取消</button>
    </div>
    <home-tips v-if="tipsStatus" :data="tipsData" @accept="tipsAccept" @refuse="tipsRefuse"></home-tips>
  </div>
</template>

<script>
import { getPairingList, addPairingRequest, acceptPairingRequest, removePairingRequest } from 'network/Home';
import HomeTips from './HomeTips.vue';

export default {
  components: { HomeTips },
  name: 'HomePairingList',
  data () {
    return {
      pairingList: [],
      myPairing: [],
      addStatus: false,
      addContent: '',
      tipsStatus: false,
      nowPairing: {},
      tipsData: {},
      type: 'default'
    }
  },
  computed: {
    showPairingList () {
      if (this.type == 'default') {
        return this.pairingList;
      } else {
        return this.myPairing;
      }
    }
  },
  methods: {
    submit () {
      addPairingRequest({
        sno: this.$store.state.user.sno,
        content: this.addContent
      }).then(res => {
        if (res.data.status) {
          alert('成功发起配对');
        } else {
          alert('发起配对失败');
        }
        let time = new Date(res.data.time);
        this.myPairing.push({
          username: res.data.username,
          time: time.getFullYear() + '/' + (time.getMonth() + 1) + '/' + time.getDate(),
          content: this.addContent,
          id: res.data.id,
          bgc: tool.randomColor(180, 220)
        });
        this.addStatus = false;
        this.addContent = '';
      }).catch(err => {
        console.log(err);
      });
    },
    searchPairing (content) {
      getPairingList({
        sno: this.$store.state.user.sno,
        type: this.type == 'default' ? 'exclude' : 'include',
        content
      }).then(res => {
        let arr = [];
        for (let pr of res.data.list) {
          let time = new Date(pr.startTime);
          arr.push({
            username: pr.username,
            time: time.getFullYear() + '/' + (time.getMonth() + 1) + '/' + time.getDate(),
            content: pr.content,
            id: pr.id,
            bgc: tool.randomColor(180, 220)
          });
        }
        if (this.type == 'default') {
          this.pairingList = arr;
        } else {
          this.myPairing = arr;
        }
      }).catch(err => {
        console.log(err);
      });
    },
    accept (pairing) {
      this.nowPairing = pairing;
      this.tipsData = {
        content: '确定要接受' + pairing.username + '的配对请求吗',
        acceptText: '确定',
        refuseText: '取消'
      };
      this.tipsStatus = true;
    },
    remove (pairing) {
      this.nowPairing = pairing;
      this.tipsData = {
        content: '确定要删除这条配对请求吗',
        acceptText: '删除',
        refuseText: '取消'
      };
      this.tipsStatus = true;
    },
    tipsAccept () {
      if (this.type == 'default') {
        acceptPairingRequest({
          sno: this.$store.state.user.sno,
          id: this.nowPairing.id
        }).then(res => {
          if (res.data.status) {
            alert('已接受配对并向' + this.nowPairing.username + '发了好友请求');
            this.removePairing(this.nowPairing);
          } else {
            alert(res.data.message);
          }
          this.tipsStatus = false;
        }).catch(err => {
          console.log(err);
        });
      } else if (this.type == 'delete') {
        removePairingRequest({
          id: this.nowPairing.id
        }).then(res => {
          if (res.data.status) {
            alert('删除成功');
            this.removePairing(this.nowPairing);
          } else {
            alert('删除失败');
          }
          this.tipsStatus = false;
        }).catch(err => {
          console.log(err);
        });
      }
    },
    tipsRefuse () {
      this.tipsStatus = false;
    },
    deletePairing () {
      if (this.type == 'default') {
        this.type = 'delete';
      } else {
        this.type = 'default';
      }
    },
    removePairing (pairing) {
      let arr = [];
      let blarr;
      if (this.type == 'default') {
        blarr = this.pairingList;
      } else if (this.type == 'delete') {
        blarr = this.myPairing;
      } else {
        alert('数据异常');
        return;
      }
      for (let pr of blarr) {
        if (pr == pairing) {
          continue;
        }
        arr.push(pr);
      }
      if (this.type == 'default') {
        this.pairingList = arr;
      } else if (this.type == 'delete') {
        this.myPairing = arr;
      }
    }
  },
  created () {
    getPairingList({
      sno: this.$store.state.user.sno,
      type: 'exclude',
      content: ''
    }).then(res => {
      let arr = [];
      for (let pr of res.data.list) {
        let time = new Date(pr.startTime);
        arr.push({
          username: pr.username,
          time: time.getFullYear() + '/' + (time.getMonth() + 1) + '/' + time.getDate(),
          content: pr.content,
          id: pr.id,
          bgc: tool.randomColor(180, 220)
        });
      }
      this.pairingList = arr;
    }).catch(err => {
      console.log(err);
    });
    getPairingList({
      sno: this.$store.state.user.sno,
      type: 'include',
      content: ''
    }).then(res => {
      let arr = [];
      for (let pr of res.data.list) {
        let time = new Date(pr.startTime);
        arr.push({
          username: pr.username,
          time: time.getFullYear() + '/' + (time.getMonth() + 1) + '/' + time.getDate(),
          content: pr.content,
          id: pr.id,
          bgc: tool.randomColor(180, 220)
        });
      }
      this.myPairing = arr;
    }).catch(err => {
      console.log(err);
    });
  }
}
</script>

<style>
.pairingListBox {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 15px;
  box-sizing: border-box;
}

.pairingListBox > .pairingListBoxMain {
  width: 100%;
  height: 100%;
  padding-right: 5px;
  border-radius: 10px;
  /* box-sizing: border-box; */
  overflow-y: scroll;
}

.pairingListBox > .pairingListBoxMain::-webkit-scrollbar {
  width: 6px;
}

.pairingListBox > .pairingListBoxMain::-webkit-scrollbar-thumb {
  background-color: #999;
  border-radius: 10px;
}

.pairingListBox > .pairingListBoxMain:hover::-webkit-scrollbar-thumb {
  background-color: #666;
}

.pairingListBox > .pairingListBoxMain > ul {
  column-count: 3;
  column-gap: 15px;
}

.pairingListBox > .pairingListBoxMain > ul > li {
  position: relative;
  break-inside: avoid;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 15px;
  box-sizing: border-box;
  background-color: skyblue;
  cursor: default;
  transition: transform 0.3s;
}

.pairingListBox > .pairingListBoxMain > ul > li:hover {
  transform: translate(0px, -3px);
}

.pairingListBox > .pairingListBoxMain > ul > li > .username {
  display: inline-block;
  height: 24px;
  padding: 2px 20px;
  border-radius: 50px;
  font-size: 20px;
  line-height: 24px;
  background-color: #eee;
}

.pairingListBox > .pairingListBoxMain > ul > li > .username > .usernameMain {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(45deg, rgb(2, 172, 240), rgb(255, 79, 109));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.pairingListBox > .pairingListBoxMain > ul > li > .content {
  display: block;
  width: 100%;
  border: 0px;
  margin: 5px 0px 15px;
  font-size: 16px;
  background-color: transparent;
  resize: none;
  outline: none;
  white-space: pre-line;
}

.pairingListBox > .pairingListBoxMain > ul > li > .time {
  font-size: 12px;
  color: #666;
}

.pairingListBox > .pairingListBoxMain > ul > li > .accept {
  position: absolute;
  bottom: 20px;
  right: 20px;
  padding: 3px 10px;
  border-radius: 5px;
  color: #fff;
  background-color: #3fc079;
  cursor: pointer;
  transition: background-color 0.3s;
}

.pairingListBox > .pairingListBoxMain > ul > li > .accept:hover {
  background-color: #439b6a;
}

.pairingListBox > .pairingListBoxMain > ul > li > .delete {
  position: absolute;
  bottom: 20px;
  right: 20px;
  padding: 3px 10px;
  border-radius: 5px;
  color: #fff;
  background-color: #ff4141;
  cursor: pointer;
  transition: background-color 0.3s;
}

.pairingListBox > .pairingListBoxMain > ul > li > .delete:hover {
  background-color: #d63f3f;
}

.pairingListBox > .addPairingRequest {
  position: absolute;
  bottom: 30px;
  right: 30px;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  color: #eee;
  background-image: url("~assets/img/friendListAdd.png");
  background-repeat: no-repeat;
  background-size: 70%;
  background-position: center center;
  background-color: #ff8656;
  cursor: pointer;
  transition: background-color 0.3s;
}

.pairingListBox > .addPairingRequest:hover {
  background-color: #f05418;
}

.pairingListBox > .deletePairingRequest {
  position: absolute;
  bottom: 80px;
  right: 30px;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  color: #eee;
  background-image: url("~assets/img/friendListRemove.png");
  background-repeat: no-repeat;
  background-size: 70%;
  background-position: center center;
  background-color: #ff8656;
  cursor: pointer;
  transition: background-color 0.3s;
}

.pairingListBox > .deletePairingRequest:hover {
  background-color: #f05418;
}

.pairingListBox > .baffles {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.pairingListBox > .addPairingRequestBox {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 360px;
  height: 420px;
  padding: 20px 30px;
  border-radius: 10px;
  box-shadow: 0px 0px 5px 0px #fff;
  box-sizing: border-box;
  background-color: #fff;
  transform: translate(-50%, -50%);
}

.pairingListBox > .addPairingRequestBox > .title {
  margin: 10px 0px 30px;
  font-size: 30px;
  text-align: center;
  background-image: linear-gradient(45deg, rgb(2, 172, 240), rgb(255, 79, 109));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.pairingListBox > .addPairingRequestBox > textarea {
  display: block;
  width: 100%;
  height: 240px;
  padding: 10px;
  border: 1px solid #666;
  border-radius: 10px;
  box-sizing: border-box;
  font-size: 24px;
  resize: none;
  outline: none;
}

.pairingListBox > .addPairingRequestBox > button {
  position: absolute;
  bottom: 24px;
  width: 80px;
  height: 30px;
  border-radius: 10px;
  border: 0px;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s;
}

.pairingListBox > .addPairingRequestBox > .submit {
  left: 50px;
  background-color: #3fc079;
}

.pairingListBox > .addPairingRequestBox > .submit:hover {
  background-color: #439b6a;
}

.pairingListBox > .addPairingRequestBox > .close {
  right: 50px;
  background-color: #ff8656;
}

.pairingListBox > .addPairingRequestBox > .close:hover {
  background-color: #f05418;
}
</style>


